.intro {
  position: relative;

  overflow: hidden;

  background-color: #2b2d5c;
  background-image: url(../img/bg-intro.jpg);
  background-repeat: no-repeat;
  background-position: 50% 0;

  &::before,
  &::after {
    position: absolute;
    bottom: 0;

    content: "";

    background-repeat: no-repeat;
  }

  &::before {
    left: 0;

    width: 158px;
    height: 159px;

    background-image: url(../img/decor-left.png);
  }

  &::after {
    right: 0;

    width: 153px;
    height: 156px;

    background-image: url(../img/decor-right.png);
  }
}

.intro__container {
  position: relative;

  min-height: 900px;
  padding-top: 352px;

  @include container;

  &::before,
  &::after {
    position: absolute;

    content: "";

    background-color: #ffffff;
  }

  &::before {
    top: 146px;
    left: 123px;

    width: 1px;
    height: 124px;
  }

  &::after {
    top: 208px;
    left: 63px;

    width: 124px;
    height: 1px;
  }
}

.intro__title {
  width: 470px;
  margin: 0;

  font-family: "Trirong", "Georgia", serif;
  font-size: 60px;
  font-weight: 300;
  line-height: 72px;

  color: #ffffff;
}

.intro__text {
  margin: 0;
  margin-top: 15px;

  font-family: "Muli", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 200;
  line-height: 32px;

  color: #ffffff;
}

.intro__works-link {
  width: 220px;
  height: 60px;
  margin-top: 45px;

  font-size: 16px;
  
  line-height: 30px;
  color: #2b2d5c;
}

.intro__social {
  position: absolute;
  z-index: 1;
  right: 0;
  bottom: 0;

  height: 86px;
  margin: 0;
  padding-left: 83px;

  border-top: 1px solid #ffffff;

  @media (min-width: 1600px) {
    left: 50%;

    margin-left: 417px;
  }

  &::before {
    display: block;

    width: 1px;
    height: 278px;
    margin-right: 44px;

    content: "";

    background-color: #ffffff;
  }
}

.intro__scroll {
  position: relative;

  display: block;
  overflow: hidden;

  width: 30px;
  height: 30px;
  margin-top: 157px;
  margin-right: auto;
  margin-left: auto;
  padding: 5px;

  align-self: center;

  &::before,
  &::after {
    position: absolute;
    top: 9px;
    left: 9px;

    display: block;

    width: 20px;
    height: 20px;

    content: "";
    transform: rotate(45deg);

    border-top: none;
    border-right: 1px solid #ffffff;
    border-bottom: 1px solid #ffffff;
    border-left: none;
  }

  &::after {
    top: 0;

    border-right: 1px solid #f2c200;
    border-bottom: 1px solid #f2c200;
  }
}
