.latest-posts {
  font-family: "Muli", "Arial", sans-serif;
}

.latest-posts__title {
  font-weight: 300;
  font-size: 25px;
  line-height: 50px;
  margin: 0;
  color: #ffffff;
  margin-bottom: 13px;
}

.latest-posts__item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
  
  &:last-child {
    margin-bottom: 12px; 
  }
}

.latest-posts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.latest-posts__post-title {
  margin: 0;
  margin-top: -4px;
  line-height: 20px;
  width: 270px;
  margin-bottom: 5px;
}

.latest-posts__link {
  font-family: "Muli", "Arial", sans-serif; 
  outline: none;
  font-weight: 300;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.latest-posts__info {
  font-weight: 300;
  font-size: 14px;
  line-height: 28px;
  color: #ffffff;
  margin: 0;
}

.latest-posts__type {
  font-weight: 600;
}

.latest-posts__image {
  order: -1;
  margin-right: 30px;
}

.latest-posts__more-link {
  margin-left: 100px;
  font-family: "Muli", "Arial", sans-serif; 
  outline: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}
