.clients {
  background-image: url(../img/bg-clients.jpg), linear-gradient(to right, #3b3c67 50%, #393a65 50%);
  background-repeat: no-repeat;
  background-position: center 0;
  margin-top: 100px;

  background-color: #393a65;
}

.clients__container {
  min-height: 297px;
  padding-top: 71px;

  text-align: center;

  @include container;
}

.clients__title {
  margin: 0;

  font-family: "Trirong", "Georgia", serif;
  font-size: 40px;
  font-weight: 300;
  line-height: 30px;

  color: #ffffff;
}

.clients__list {
  display: flex;

  margin: 0;
  margin-top: 33px;
  padding: 0;

  list-style: none;
}

.clients__item {
  position: relative;

  width: 175px;
  height: 80px;

  background-repeat: no-repeat;
  background-position: center;

  &--salon-centric {
    width: 163px;
    margin-right: 48px;

    background-image: url("../img/client-salon-centric.png");
  }

  &--luscious {
    width: 152px;
    margin-right: 66px;

    background-image: url("../img/client-luscious.png");
    background-position: center 9px;
  }

  &--brillbird {
    width: 130px;
    margin-right: 54px;

    background-image: url("../img/client-brillbird.png");
  }

  &--loxa-beauty {
    margin-right: 68px;

    background-image: url("../img/client-loxa-beauty.png");
    background-position: center 27px;
  }

  &--minkys {
    width: 104px;

    background-image: url("../img/client-minkys.png");
    background-position: center 0;
  }

  &--babaria {
    width: 110px;

    background-image: url("../img/client-babaria.png");
  }

  &:last-child {
    margin-left: auto;
  }
}

.clients__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
