.quick-link__title {
  font-family: "Muli", "Arial", sans-serif;    
  font-weight: 300;
  font-size: 25px;
  line-height: 50px;
  margin: 0;
  color: #ffffff;
  margin-bottom: 9px;
}

.quick-link__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.quick-link__link {
  outline: none;
  font-family: "Muli", "Arial", sans-serif;    
  font-weight: 600;
  font-size: 14px;
  line-height: 30px;
  color: #ffffff;
  text-decoration: none;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}
