.portfolio {
  margin-top: 100px;
}

.portfolio__container {
  min-height: 894px;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  // justify-content: space-between;
  width: 100%;
  min-width: 1300px;
  max-width: 1600px;
  margin-right: auto;
  margin-left: auto;
}

.portfolio__text-wrapper {
  box-sizing: border-box;
  background-color: #f2c200;
  // width: 485px;
  width: 30.3%;
  min-height: 841px;
  padding-top: 203px;
  padding-left: 77px;
  padding-right: 70px;
  position: relative;

  &::after {
    content: "";
    top: 52px;
    left: 0;
    position: absolute;
    display: block;
    width: 110.3%;
    // width: 534px;
    height: 100%;
    // background-color: rgba(255, 0, 0, 0.2);
    box-sizing: border-box;
    border: 1px solid #4d407c;
  }
}

.portfolio__title {
  margin: 0;

  font-family: "Trirong", "Georgia", serif;
  font-size: 40px;
  font-weight: 400;
  line-height: 38px;

  color: #2b2d5c;
}

.portfolio__text {
  display: block;
  padding-top: 60px;
  padding-bottom: 20px;

  margin: 0;

  font-family: "Muli", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;

  color: #2b2d5c;
}

.portfolio__read-more-link {
  font-family: "Muli", "Arial", sans-serif;
  font-weight: 600;
  line-height: 38px;
  font-size: 16px;
  color: #2b2d5c;
  text-decoration: none;
}

.portfolio__see-more-link {
  margin-top: 60px;
  margin-left: 46px;
  font-weight: 600;
  font-size: 16px;
  line-height: 38px;
  color: #f2c200;
  text-transform: uppercase;
}
