.post {
  position: relative;
  z-index: 1;

  width: 30.6%;
  // margin-top: -245px;
  padding-bottom: 58px;

  font-family: "Muli", "Arial", sans-serif;
  font-weight: 300;
  line-height: 28px;

  text-align: left;

  color: #2b2d5c;
  background-color: #ffffff;
  min-height: 615px;
  box-sizing: border-box;

  &:first-child,
  &:last-child {
    margin-top: -245px;
  }

  &:nth-child(2) {
    // top: 245px;
    box-shadow: 0 0 80px 2px rgba(43, 46, 102, 0.2);
  }

  &:first-child {
    &::before {
      position: absolute;
      z-index: -1;
      bottom: -208px;
      left: 3px;

      width: 157px;
      height: 256px;

      content: "";

      background-image: url(../img/decor-plus.png);
      background-position: center -50px;
      background-repeat: no-repeat;
      transform: rotate(-90deg);
    }
  }

  &:last-child {
    &::before {
      position: absolute;
      z-index: -1;
      top: -313px;
      right: -3.5%;

      width: 256px;
      height: 313px;

      content: "";

      background-image: url(../img/decor-plus.png);
      background-repeat: no-repeat;
    }
  }
}

.post__wrapper {
  padding-right: 5.6%;
  padding-left: 8%;
}

.post__title {
  margin: 0;
  margin-top: 22px;

  font-size: 20px;
  font-weight: 600;
  line-height: 30px;
}

.post__info {
  margin: 0;
  margin-top: 14px;
  margin-bottom: 13px;

  font-size: 15px;
}

.post__info-type {
  font-weight: 600;
}

.post__text {
  margin: 0;
  margin-bottom: 11px;

  font-size: 16px;

  color: #595666;
}

.post__read-more-link {
  position: relative;
  z-index: 1;

  font-family: "Muli", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 32px;

  text-decoration: none;

  color: #595666;
  outline: none;

  &::after {
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 0;

    width: 96px;
    height: 11px;

    content: "";

    background-color: #ffe066;
  }

  &:hover::after,
  &:focus::after {
    top: 0;

    height: 22px;
  }
}

.post__image {
  display: block;
}
