.main-nav__list {
  display: flex;

  margin: 0;
  margin-top: -4px;
  margin-right: -6px;
  padding: 0;

  list-style: none;
}

.main-nav__item {
  margin-left: 18px;
}

.main-nav__link {
  padding-right: 6px;
  padding-left: 6px;

  font-family: "Muli", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 32px;

  text-decoration: none;

  color: #ffffff;
  outline: none;

  ouline: none;

  &::after {
    display: block;
    visibility: hidden;

    width: 100%;
    height: 1px;

    content: "";

    background-color: #f2c200;
  }

  &:hover::after,
  &:focus::after {
    visibility: visible;
  }
}
