.instagram-update__title {
  font-family: "Muli", "Arial", sans-serif;    
  font-weight: 300;
  font-size: 25px;
  line-height: 50px;
  margin: 0;
  color: #ffffff;
  margin-bottom: 15px;
  letter-spacing: -0.02em;
}

.instagram-update__list {
  display: flex;
  flex-wrap: wrap;
  width: 200px;
  justify-content: space-between;
}

.instagram-update__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 96px;
  margin-bottom: 10px;
}

.instagram-update__image {
  display: block;
}
