.message-form__item {
  margin: 0;
}

.message-form__field {
  box-sizing: border-box;
  width: 100%;
  height: 45px;
  margin-bottom: 26px;
  padding: 10px 27px;

  font-family: "Muli", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 300;
  line-height: 34px;

  border: none;
  outline: none;
  background-color: #b6b7c7;

  &:focus {
    background-color: #ffffff;
  }

  &--message {
    min-height: 87px;

    resize: none;
    margin-bottom: 0;
  }

  &::-webkit-input-placeholder {
    opacity: 1;
    color: #2b2d5c;
  }

  &::-moz-placeholder {
    opacity: 1;
    color: #2b2d5c;
  }

  &:-moz-placeholder {
    opacity: 1;
    color: #2b2d5c;
  }

  &:-ms-input-placeholder {
    opacity: 1;
    color: #2b2d5c;
  }
}

.message-form__submit {
  margin-top: 30px;
  width: 200px;
  height: 49px;
  margin-right: auto;
  margin-left: auto;

  font-size: 15px;
  line-height: 34px;
  font-weight: 300;

  color: #2b2d5c;
  text-transform: uppercase;
}
