.lets-work {
  background-color: #2f315f;
  min-height: 528px;
  border-bottom: 1px solid #55577d;
  margin-top: 100px;
  background-image: url(../img/bg-form.jpg);
  background-position: center 0;
  background-repeat: no-repeat;
}

.lets-work__container {
  @include container;
  min-height: 528px;
  padding-top: 60px;
  
  display: flex;
  flex-direction: column;
}

.lets-work__title {
  text-align: center;
  font-family: "Trirong", "Georgia", serif;
  font-weight: 300;
  color: #ffffff;
  font-size: 40px;
  line-height: 38px;
  margin: 0;
  margin-bottom: 44px;
}

.lets-work__form {
  width: 501px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: auto;
}

.lets-work__top-link {
  width: 70px;
  height: 57px;
  background-color: #cbcbd7;
  margin-left: auto;
  position: relative;
  outline: none;

  &:hover,
  &:focus {
    background-color: #ffffff;
  }

  &::before,
  &::after {
    position: absolute;
    content: "";
    border: 2px solid #2c2e5d;
    width: 10px;
    height: 10px;
    transform: rotate(45deg);
    top: 23px;
    left: 29px;
    border-bottom: none;
    border-right: none;
  }

  &::after {
    top: 30px;
  }
}
