.about {
  margin-top: 100px;
  background-image: linear-gradient(to bottom, #f7f7fc 781px, transparent 781px);
}

.about__container {
  min-height: 730px;
  display: flex;

  box-sizing: border-box;
  width: 100%;
  min-width: 1300px;
  max-width: 1600px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 65px;

  justify-content: space-between;
}

.about__title {
  margin: 0;

  font-family: "Trirong", "Georgia", serif;
  font-size: 40px;
  font-weight: 300;
  line-height: 76px;

  color: #2b2d5c;
}

.about__text {
  width: 735px;
  margin: 0;
  margin-top: 5px;
  margin-bottom: 24px;

  font-family: "Muli", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;

  color: #595666;
}

.about__image-wrapper {
  width: 33.5%;  
}

.about__image {
  display: block; 
}


.about__content-wrapper {
  margin-right: auto;
  margin-left: 135px;
  padding-top: 44px;
}

.about__subtitle {
  margin: 0;

  font-family: "Trirong", "Georgia", serif;
  font-size: 26px;
  font-weight: 300;
  line-height: 38px;

  color: #2b2d5c;
}

.about__skill-list {
  margin: 0;
  margin-top: 15px;
}

.about__flickr-link {
  margin-top: 55px;
  width: 284px;
  height: 64px;

  text-transform: uppercase;
  font-size: 14px;
  line-height: 76px;
}
