.works {
  display: flex;

  // width: 1065px;
  width: 66.6%;
  margin: 0;
  margin-left: auto;
  padding: 0;

  list-style: none;

  flex-wrap: wrap;

  align-items: flex-start;
  box-shadow: -50px -111px 0 0 #f7f7fc;
}

.works__left-column {
  display: flex;
  flex-wrap: wrap;
  // flex-direction: column;
  width: 34.7%;
  // min-height: 749px;
  // margin-right: 29px;
  margin-right: 2.7%;

}

.works__right-column {
  display: flex;
  width: 62.5%;
  
  // width: 62.5%;
  // width: 666px;

  flex-wrap: wrap;
}

.works__left-column .works__item {
  &:first-child {
    margin-bottom: 9.6%;
  }
}

.works__right-column .works__item {
  margin-bottom: 4.8%;
  
  &:nth-child(1) {
    margin-right: 4.5%;
    width: 70.7%;
  }

  &:nth-child(2) {
    width: 24.7%;
  }

  &:nth-child(3) {
    margin-right: 5.1%;
    width: 45.2%;
  }

  &:nth-child(4) {
    width: 49.7%;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.works__image {
  display: block;
  width: 100%;
}
