.page-header {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  left: 0;

  display: flex;
  align-items: flex-start;

  box-sizing: border-box;
  width: 100%;
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 66px;
  padding-right: 65px;
  padding-left: 65px;

  justify-content: space-between;
}

.portfolio__see-more-link {
  font-weight: 600;
  color: #f2c200;
  line-height: 38px;
  height: 51px;
  width: 217px;
  font-weight: 300;
}
