.blog {
  margin-top: 100px;

  min-height: 838px;
  background-image: linear-gradient(to bottom, #fafafd 840px, transparent 840px);
  position: relative;
}

.blog__container {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  min-width: 1300px;
  max-width: 1600px;
  
  margin-right: auto;
  margin-left: auto;
  padding-top: 32px;
  padding-right: 65px;
  padding-left: 65px;

  text-align: center;

  // &::before {
  //   content: "";
  //   position: absolute;
  //   height: 93%;
  //   border: 2px solid #ffffff;
  //   width: 100%;
  //   top: 130px;

  //   left: 50%;
  //   margin-left: -16%;
  // }
}

.blog__title {
  font-family: "Trirong", "Georgia", serif;
  font-size: 40px;
  font-weight: 300;
  line-height: 38px;

  color: #2b2d5c;
}

.blog__text {
  width: 32%;
  margin-top: 0;
  margin-right: auto;
  min-height: 160px;
  // margin-bottom: 78px;
  margin-left: auto;

  font-family: "Muli", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 300;
  line-height: 34px;

  color: #2b2d5c;
}

.blog__posts-list {
  display: flex;
  align-items: flex-start;

  margin: 0;
  padding: 0;

  list-style: none;

  justify-content: space-between;
}
