.button {
  display: flex;
  justify-content: center;
  align-items: center;
  
  box-sizing: border-box;
  
  padding-left: 10px;
  padding-right: 10px;

  font-family: "Muli", "Arial", sans-serif;
  
  cursor: pointer;
  text-decoration: none;

  
  border: none;
  border-radius: 60px;
  background-color: #f2c200;

  justify-content: center;
  align-items: center;

  &--blue {
    background-color: #2b2d5c;
    color: #fefeff;
  }

  &--transparent {
    border: 1px solid #ffcc00;
    background-color: transparent;
  }
}
