  padding: 0;
}

.skill-list__title {
  text-transform: uppercase;
  font-family: "Muli", "Arial", sans-serif;
  font-size: 16px;
  line-height: 38px;
  font-weight: 600;
  color: #595666;
  margin: 0;
  margin-bottom: 8px;
}
