.social {
  display: flex;

  box-sizing: border-box;

  list-style: none;

  align-items: center;

}

.social__item {
  margin-right: 22px;

  &--footer {
    margin-right: 9px;
  }
}

.social__link {
  display: flex;
  overflow: hidden;

  width: 22px;
  height: 23px;

  outline: none;

  align-items: center;
  justify-content: center;

  &:hover .social-button__icon,
  &:focus .social-button__icon {
    fill: #f2c200;
  }
}

.social-button__icon {
  fill: #ffffff;
}
