.page-footer {
  background-color: #2b2d5c;
}

.page-footer__container {
  padding-top: 79px;
  @include container;
  display: flex;
  justify-content: space-between;
  padding-bottom: 27px;
}

.page-footer__column {
  box-sizing: border-box;
  &:first-child {
    width: 230px;
  }

  &:nth-child(2) {
    width: 400px;
  }

  &:nth-child(3) {
    width: 130px;;
  }

  &:last-child {
    width: 200px;
  }
}

.page-footer__logo {
  margin-top: 10px;
}

.page-footer__social-wrapper {
  margin-top: 37px;
}

.page-footer__social-title {
  font-family: "Muli", "Arial", sans-serif;
  color: #ffffff;

  font-weight: 600;
  font-size: 20px;
  line-height: 22px;
  margin: 0;
}

.page-footer__social {
  padding: 0;
  margin: 0;
  margin-top: 5px;
  margin-left: -5px;
}

.page-footer__copyright {
  box-sizing: border-box;
  min-height: 62px;
  margin: 0;
  padding-top: 10px;
  width: 100%;
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.1);

  font-family: "Muli", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.25);

  font-weight: 200;
  font-size: 14px;
  line-height: 38px;
}
